.controls{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    width:auto;
    background: rgb(190, 190, 190);
    margin-right: 5%;
    color: rgb(7, 44, 53);
    font-weight: 900;
    text-transform: uppercase;
  }
.controls__multiplayer{
  color:rgb(170, 38, 247);
  font-size: 40px;
  cursor: pointer;
}
.controls__raise{
  border-radius: 10px;
  height:40px;
  font-size:20px;
  width:80%;
  cursor:pointer;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  text-transform: uppercase;
}

.controls__socket{
  color: darkred;
  font-weight: 900;
  font-size: 10px;
  text-transform: uppercase;
}

.controls__resetPause{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width:100%;
}
.controls__resetPause__reset{
  color: rgb(59, 9, 153);
  font-size: 40px;
  cursor: pointer;
}

.controls__resetPause__pause{
  color:red;
  font-size: 40px;
  cursor: pointer;
}

.controls__resetPause__play{
  color:green;
  font-size: 40px;
  cursor: pointer;
}



.controls__resetPause__reset:hover, .controls__resetPause__pause:hover, .controls__resetPause__play:hover, .controls__multiplayer:hover {
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
  -webkit-transform: scale(1.25);
      -ms-transform: scale(1.25);
          transform: scale(1.25);
}

.controls__raise:hover{
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
  -webkit-transform: translateY(-5px);
      -ms-transform: translateY(-5px);
          transform: translateY(-5px);
  -webkit-box-shadow: 5px 10px;
          box-shadow: 5px 10px;
}



